<template>
<div class="edit-address">
	<van-nav-bar title="编辑收货地址" left-text="返回" left-arrow @click-left="onClickLeft" />
	<van-address-edit
		:address-info="addressInfo"
		:area-list="areaList"
		show-postal
		show-delete
		show-set-default
		show-search-result
		@save="saveAddress"
		@delete="removeAddress"
		/>
</div>
</template>

<script>
import area from '../area'
import {
	mapGetters
} from 'vuex'
import { utils } from '../assets/js/utils'

export default {
	data() {
		return {
			aid: '',
			addressInfo: {},
			areaList: area
		}
	},
	created() {
		this.aid = this.$route.params.aid;
		this.findAddress();
	},
	methods: {
		// 删除地址
		removeAddress() {
			if(!this.getToken) {
				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			let data = utils.queryString({
				appkey: this.appkey,
				tokenString: this.getToken,
				aid: this.aid
			})

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'POST',
				url: '/deleteAddress',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				this.$toast({
					message: res.data.msg,
					duration: 500
				});
				this.$router.replace({
					name: 'MyAddress'
				});
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
		},
		// 保存地址
		saveAddress(content) {
			if(!this.getToken) {
				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			let obj = Object.assign({},content);

			let arr = ['country','createdAt','id','isRemove','updatedAt','userId'];
			for(let i = 0; i < arr.length; i++) {
				delete obj[arr[i]]
			}

			obj.appkey = this.appkey
			obj.tokenString = this.getToken
			obj.isDefault = Number(obj.isDefault)
			
			let data = utils.queryString(obj)

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'POST',
				url: '/editAddress',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				this.$toast(res.data.msg)
				if(res.data.code === 30000) {
					this.$router.replace({
						name: 'MyAddress'
					})	
				}
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})

		},
		// 查询地址
		findAddress() {
			if(!this.getToken) {
				this.$router.push({
					name: 'Login'
				})

				return ;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken,
				aid: this.aid
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			})

			this.axios({
				method: 'GET',
				url: '/findAddressByAid',
				params
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 40000) {
					res.data.result[0].isDefault = Boolean(res.data.result[0].isDefault);
					this.addressInfo = res.data.result[0];
				}
				if(res.data.code === 40001) {
					this.$toast(res.data.msg);
				}
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})
		},
		onClickLeft() {
			this.$router.go(-1);
		}
	},
	computed: {
		...mapGetters(['getToken'])
	}
}
</script>

<style lang="scss" scoped>
.edit-address {

	/deep/ .van-nav-bar__text,
	/deep/ .van-nav-bar .van-icon {
		color: #323233;
	}

	/deep/ .van-button--danger {
		border: 0.02667rem solid #2b4868;
		background-color: #2b4868;
	}
}
</style>